@import url('https://fonts.googleapis.com/css2?family=Vazirmatn:wght@500&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  font-family: 'Vazirmatn', sans-serif;
}

body {
  background-color: #F7F5EB;
  color: #2D2727;
  margin: 0rem 1rem;
}

#header {
  width: 100%;
  justify-content: center;
  display: flex;
}

#header button {
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 21px;
  display: flex;
  width: 9rem;
  height: 3.5rem;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  background-color: #EAE0DA;
  color: #2D2727;
}

#server {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  height: 4rem;
  justify-content: space-between;
  align-items: center;
  background-color: #EAE0DA;
  border-radius: 16px;
  padding: 0 1.5rem 0 .75rem;
  margin-bottom: .75rem;
}

.copy-btn {
  width: 4rem;
  height: 2rem;
  background-color: #9EA1D4;
  border-radius: 12px;
  color: #F7F5EB;
}

.copy-btn:active {
  background-color: #9ea1d4cc;
}

#bar {
  width: calc(100% - 4rem);
  background-color: #EAE0DA;
  height: 5rem;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  margin-bottom: 3.5rem;
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: space-between;
  align-items: center;
  padding: .75rem;
  border-radius: 32px;
}

.servers-homepage, .education-homepage {
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.servers-homepage {
  color: #2D2727;
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.education-homepage {
  color: #2D2727;
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.nav-btn-left {
  background-color: #FD8A8A;
  width: calc(50% - .75rem);
  height: 3.5rem;
  border-radius: 16px;
  position: absolute;
  left: 0;
  z-index: -1;
  margin-left: .75rem;
  transform: translate(0);
  animation: nav-btn-to-left .5s;
}

.nav-btn-right {
  background-color: #FD8A8A;
  width: calc(50% - .75rem);
  height: 3.5rem;
  border-radius: 16px;
  position: absolute;
  left: 100%;
  transform: translate(-107%);
  z-index: -1;
  margin-right: 1rem;
  animation: nav-btn-to-right .5s;
}

.nav-btn-black-right {
  color: #F7F5EB;
}

.nav-btn-black-left {
  color: #F7F5EB;
}

#education {
  display: none;
}

@keyframes nav-btn-black-right {
  0% {
    color: #2D2727;
  }
  100% {
    color: #F7F5EB;
  }
}

@keyframes nav-btn-to-right {
  0% {
    margin-left: .75rem;
    position: absolute;
    left: 0;
    transform: translate(0);
  }
  100% {
    margin-right: 1rem;
    position: absolute;
    left: 100%;
    transform: translate(-107%);
  }
}

@keyframes nav-btn-to-left {
  0% {
    background-color: #FD8A8A;
    width: calc(50% - .75rem);
    height: 3.5rem;
    border-radius: 16px;
    position: absolute;
    left: 100%;
    transform: translate(-107%);
    z-index: -1;
    margin-right: 1rem;
  }
  100% {
    background-color: #FD8A8A;
    width: calc(50% - .75rem);
    height: 3.5rem;
    border-radius: 16px;
    position: absolute;
    left: 0;
    z-index: -1;
    margin-left: .75rem;
    transform: translate(0);
  }
}